@font-face {
  font-family: "National2";
  src: url("../fonts/National2/National2-Light.eot");
  src: url("../fonts/National2/National2-Light.eot?#iefix") format("embedded-opentype"), url("../fonts/National2/National2-Light.woff") format("woff"), url("../fonts/National2/National2-Light.ttf") format("truetype"), url("../fonts/National2/National2-Light.svg#National2") format("svg");
  font-weight: normal;
}
@font-face {
  font-family: "National2-Regular";
  src: url("../fonts/National2/National2-Regular.eot");
  src: url("../fonts/National2/National2-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/National2/National2-Regular.woff") format("woff"), url("../fonts/National2/National2-Regular.ttf") format("truetype"), url("../fonts/National2/National2-Regular.svg#National2-Regular") format("svg");
  font-weight: normal;
}
@font-face {
  font-family: "National2-Bold";
  src: url("../fonts/National2/National2-Bold.eot");
  src: url("../fonts/National2/National2-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/National2/National2-Bold.woff") format("woff"), url("../fonts/National2/National2-Bold.ttf") format("truetype"), url("../fonts/National2/National2-Bold.svg#National2-Bold") format("svg");
  font-weight: normal;
}
@media screen {
  * {
    box-sizing: border-box;
  }

  html {
    height: 100%;
    padding: 0;
    margin: 0;
  }

  body {
    background-color: #fff;
    font-family: "National2", verdana, sans-serif;
    font-size: 1.25rem;
    line-height: 1.875rem;
    margin: 0;
    word-wrap: break-word;
  }

  a:link {
    text-decoration: none;
    color: #328060;
  }

  a:visited {
    color: #328060;
  }

  a:active,
a:hover {
    color: #003c73;
  }

  h1 {
    font-size: 2.1875rem;
    line-height: 3.28125rem;
  }

  h2 {
    font-size: 1.5625rem;
    line-height: normal;
  }

  h3 {
    font-size: 1.4375rem;
    line-height: 2.15625rem;
  }

  h4 {
    font-size: 1.40625rem;
  }

  img {
    max-width: 100%;
    height: auto;
  }

  /* @group ----- Navigation ----- */
  #skipnav {
    position: fixed;
    top: 1.875rem;
    left: 50%;
    border: 1px solid #ababab;
    transform: translateX(-50%);
    z-index: -5;
    padding: 0.46875rem;
    background-color: #fff;
    opacity: 0;
  }
  #skipnav:focus {
    z-index: 1000;
    opacity: 1;
  }

  /* @end */
  /* @group ----- Page Wrapper */
  .page-wrapper {
    position: relative;
    min-height: 100vh;
  }

  /* @group ----- Header ----- */
  .site-title h1 {
    margin: 0;
    font-size: 1.5625rem;
    line-height: normal;
    padding-right: 1.875rem;
  }

  header {
    margin: 0 0 3.75rem 0;
    padding: 0 5%;
    background-color: #00693e;
    border-bottom: 1px solid #dedede;
    position: relative;
  }
  header > * {
    max-width: 1450px;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
    display: block;
  }

  .site-title:hover,
.site-title:active {
    border-bottom: 0;
  }

  header h1 {
    width: 90%;
    margin: 0 0 0.46875rem 0;
    padding: 0.8rem 0;
    line-height: 2.1rem;
  }
  header h1 a,
header h1 a:active,
header h1 a:hover,
header h1 a:focus,
header h1 a:link,
header h1 a:visited {
    color: #fff;
  }
  header h1 img {
    float: left;
    max-height: 2.5rem;
    padding-right: 1rem;
  }

  /* @end */
  /* @group ----- Footer ----- */
  footer {
    width: 100%;
    padding: 1rem 0;
    clear: both;
    background-color: #333;
  }
  footer > * {
    max-width: 1450px;
    margin-left: auto;
    margin-right: auto;
    width: 90%;
  }
  footer a,
footer a:active,
footer a:focus,
footer a:hover,
footer a:link,
footer a:visited {
    color: #fff;
  }
  footer a:focus,
footer a:hover {
    text-decoration: underline;
  }

  /* @end */
  /* @group ----- Page and Blocks ----- */
  #content {
    max-width: 1450px;
    margin-left: auto;
    margin-right: auto;
    width: 90%;
    padding-bottom: 8rem;
    line-height: normal;
    color: #333;
    font-size: 1.125rem;
  }
  #content .site-promo {
    clear: both;
    padding-top: 2rem;
  }
  #content .site-promo:first-of-type {
    padding-top: 0;
  }
  #content .site-promo:after, #content .site-promo:before {
    content: " ";
    display: table;
  }
  #content h2 {
    margin: 0;
  }
  #content .site-image {
    float: left;
    margin-right: 1.5rem;
    max-width: 250px;
    border: none;
  }
  #content .site-image figure {
    margin: 0;
  }
  #content .site-image figure img {
    width: 250px;
    height: 250px;
    margin-top: 0.4em;
    object-fit: cover;
  }
  #content .site-image figure figcaption p {
    margin: 0.5rem 0;
    font-size: 1rem;
    line-height: normal;
    font-style: italic;
  }
  #content p {
    max-width: 1100px;
  }

  /* @end */
}
@media screen and (max-width: 767px) {
  #content h2 {
    font-size: 1rem;
  }
  #content .site-image {
    max-width: 150px;
  }
  #content .site-image figure img {
    width: 150px;
    height: 150px;
    margin-top: 0.2em;
  }
}
@media screen and (max-width: 575px) {
  header h1 {
    font-size: 1.5rem;
  }
}